:root{--bg: #f4f1de;--text-1: #313349;--text-2: #3a3a41;--accent-1: #E07A5F;--button-1: #313349}

body{background-color:var(--bg);margin-left:5vw;margin-right:5vw}main{background-color:var(--bg);margin-left:5vw;margin-right:5vw}*{color:var(--text-1)}.container{background-color:var(--bg);position:relative;display:flex;justify-content:center}.container .page{position:absolute;border:solid;border-color:turquoise}.container .page .inner{display:flex;justify-content:center}.container .page .inner h1{font-family:"Bebas Neue";font-size:52px;letter-spacing:0.1rem}.container .page .inner h1 .line-wrap{overflow:hidden;background-color:red;height:66px}.container .page .inner p{font-family:"Poppins";margin-top:200px;width:340px;font-weight:300;line-height:24px;font-size:14px}.other{height:20vh}.main-container{top:0;margin-top:0;min-height:100vh;padding:0 5vw;background-color:var(--bg)}.workContainer{width:100%;display:flex;flex-direction:row;font-size:2rem;font-family:avocadoBold}.logoContainerSpacer{width:8rem;text-align:right;padding-right:0;margin-right:0;left:0;padding-left:0;margin-left:0;display:flex;padding-right:0.4em}.logoContainerSpacer li{list-style:none;text-align:right;right:0;overflow-x:visible}.logoContainerSpacer ul{right:0;left:0;margin:0 0 0 0;padding:0 0 0 0;text-align:right;width:100%}.solid{border-top:1px solid #bbb;width:75vw;opacity:0.5}.sectionMenuItem{margin-top:0;padding-top:0;top:0;margin-left:5rem;y:-500}.sectionMenuItem h1{margin-top:0rem;padding-top:0;margin-bottom:1rem;padding-bottom:0;top:0;bottom:0;font-family:avocadoBold}.sectionMenuItem h2{margin-top:0;padding-top:0;top:0;font-family:avocadoBold;font-size:1rem;opacity:0.5}.activeWorkLink{color:#e07a5f;opacity:1;font-weight:bold;text-decoration:underline}.workLink{opacity:1}html,body,#app{height:100%;width:100%;margin:0;padding:0}.main-nav{display:flex;flex-direction:row;justify-content:center;height:100vh;align-items:center}html{background-size:cover;color:#fff;overflow:hidden;--easing: cubic-bezier(0.5, 0.5, 0.2, 1)}.image-hover{display:block;color:white;text-decoration:none;text-align:center;padding:2vh 0;-webkit-transform-origin:top left;transform-origin:top left;-webkit-animation:skwoop 0.7s cubic-bezier(0.5, 0.5, 0.2, 1) backwards;animation:skwoop 0.7s cubic-bezier(0.5, 0.5, 0.2, 1) backwards;-webkit-animation:skwoop 0.7s var(--easing) backwards;animation:skwoop 0.7s var(--easing) backwards;-webkit-animation-duration:calc(0.6s + (var(--i) * 0.1s));animation-duration:calc(0.6s + (var(--i) * 0.1s));-webkit-animation-delay:calc((var(--i) * 50ms));animation-delay:calc((var(--i) * 50ms));cursor:pointer}@-webkit-keyframes skwoop{from{opacity:0;-webkit-transform:translateY(5vh) skewY(5deg);transform:translateY(5vh) skewY(5deg)}}@keyframes skwoop{from{opacity:0;-webkit-transform:translateY(5vh) skewY(5deg);transform:translateY(5vh) skewY(5deg)}}.image-hover .title{font-weight:900;font-size:16em;letter-spacing:0.05ch}.image-hover .title::after{content:attr(data-title);position:absolute;top:0;left:0;z-index:5;color:transparent;-webkit-text-stroke:2px #fff}.image-hover>img{position:fixed;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);max-width:40%;max-height:60vh;pointer-events:none;z-index:2;visibility:hidden;opacity:0;mix-blend-mode:exclusion;transition:opacity 0.2s linear, visibility 0s linear 0.2s}.image-hover:hover,.image-hover:focus{z-index:10}.image-hover:hover .title::after,.image-hover:focus .title::after{display:block}.image-hover:hover>img,.image-hover:focus>img{transition-delay:0s;visibility:visible;opacity:1}.image-hover>img{--x-px: calc(var(--x) * 1px);--y-px: calc(var(--y) * 1px);top:0;left:0;-webkit-transform:translate(var(--x-px), var(--y-px)) translate(-50%, -50%);transform:translate(var(--x-px), var(--y-px)) translate(-50%, -50%)}.style1{font-family:forestFont}.style2{font-family:safariFont}.style3{font-family:oceanFont}

