.sectionMenu{margin-top:0;padding-top:0;overflow-y:hidden;opacity:0}.sectionMenu.is-reveal{opacity:1}

.section-header{text-transform:uppercase;font-weight:600;letter-spacing:1px;font-family:'Syncopte', Tahoma, Geneva, Verdana, sans-serif}

.footer{text-align:center;padding-bottom:200px}.footer .location{font-size:18vw;text-transform:uppercase;font-family:"Bodoni Moda", serif;opacity:0}.footer .location.is-reveal{opacity:1}

.featured-section{display:grid;grid-template-columns:1fr 3fr;align-items:center}.featured-section img{-webkit-clip-path:inset(100% 0% 0% 0%);clip-path:inset(100% 0% 0% 0%);transition:-webkit-clip-path 1s cubic-bezier(0.77, 0, 0.175, 1);transition:clip-path 1s cubic-bezier(0.77, 0, 0.175, 1);transition:clip-path 1s cubic-bezier(0.77, 0, 0.175, 1), -webkit-clip-path 1s cubic-bezier(0.77, 0, 0.175, 1)}.featured-section img.is-reveal{-webkit-clip-path:inset(0% 0% 0% 0%);clip-path:inset(0% 0% 0% 0%)}.featured-section h6{font-size:22px;text-transform:uppercase;font-weight:500}.featured-section .featured-row-layout,.featured-section .featured-column-layout{display:grid;grid-gap:10px;gap:10px}.featured-section .featured-row-layout{grid-template-rows:repeat(2, auto)}.featured-section .featured-row-layout img{width:100%;object-fit:cover}.featured-section .featured-column-layout{grid-template-columns:100px auto;align-items:flex-end}.featured-section .featured-column-layout img{width:100%;height:125vh;object-fit:cover}.featured-section .featured-column-layout h6{-webkit-transform:translateX(100%) rotate(-90deg);transform:translateX(100%) rotate(-90deg);-webkit-transform-origin:left bottom;transform-origin:left bottom;justify-self:self-end}

.about-section p{font-size:70px;line-height:1.12;opacity:0}.about-section p div{opacity:0;-webkit-transform:translate(0);transform:translate(0)}.about-section p.is-reveal{opacity:1}

.header-container{position:relative}.header-container .header-menu{position:absolute;left:0;top:100px;letter-spacing:1px;font-family:"Syncopate", sans-serif;color:var(--button-1);font-weight:600}.header-container .header-menu li{margin:10px 0}.header-container #section-text{font-size:18vw;text-align:right;font-family:"Bai Jamjuree", sans-serif;font-weight:600;color:var(--text-1);margin-top:0;margin-bottom:0}.header-container #heroDF{position:relative;color:var(--accent-1);font-family:avocado;background:none;font-size:18vw}.header-container p{font-size:8vw;text-align:right;font-family:"Bai Jamjuree", sans-serif;font-weight:600;color:var(--text-2);margin-top:0;margin-bottom:0}.lineParent{overflow:hidden}.lineChildren{-webkit-transform:translate(0, 500px);transform:translate(0, 500px)}

